import { Component, OnInit } from '@angular/core';
import { LaravelService } from '../laravel.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-listorder',
  templateUrl: './listorder.component.html',
  styleUrls: ['./listorder.component.css']
})
export class ListorderComponent implements OnInit {
	orderdetails=[];
  constructor(private lara:LaravelService,private route:Router) { }

  ngOnInit() {
  	this.lara.getlistorder().subscribe((data:any[])=>{
  		this.orderdetails=data;
  	});
  }
  url="http://localhost/billingsoftware";
payment(bno,value)
{
	console.log(value);
	var fd=new FormData;
	fd.append('billno',bno);
  	fd.append('paymode',value);
  	this.lara.uppaymentmode(fd).subscribe((d:any[])=>{
  		console.log(d);
  	});
}
custoorder(data)
{
	console.log(data.cname);
	console.log(data.cno);

	var custodata= new FormData;
	custodata.append('cname',data.cname);
	custodata.append('cno',data.cno);

	this.lara.getCustoOrderDet(custodata).subscribe((datas:any[])=>{
		console.log(datas);
		if(datas[0]=="")
		{
			alert("data could not found");
			this.route.navigate(['/listorder']);
		}
		else
		{
			this.orderdetails=datas;
		}
	});
}
}
