import { Component, OnInit } from '@angular/core';
import { LaravelService } from '../laravel.service';
import { Pipe, PipeTransform } from '@angular/core';

@Component({
  selector: 'app-purchased',
  templateUrl: './purchased.component.html',
  styleUrls: ['./purchased.component.css']
})
export class PurchasedComponent implements OnInit {
	details=[];
  mediname=[];
  billno="";
  customername="";

  fst="";

  constructor(private lara:LaravelService) {

  }

  ngOnInit() {
  	this.lara.getcustomers().subscribe((data:any[])=>{
    	//console.log(data);
    	this.details=data;
    	console.log(this.details);
  	});
    this.lara.getbillno().subscribe((d:any[])=>{
    //console.log(d[0].bno);
      this.billno=d[0].bno;
    });

    this.lara.getmedidetails().subscribe((data:any[])=>{
    //console.log(data);
    this.mediname=data;
    console.log(this.mediname);
  });
  }

  add(d)
  {
    this.customername=d.custoid;
    console.log(this.customername);

    var fd=new FormData();
     this.billno=this.billno+1;
     fd.append('custo_id',this.customername);
     fd.append('billno',this.billno);

    this.lara.insorderdetails(fd).subscribe((datas:any[])=>{
     console.log(datas[0].repply);
     });


  }

  addorder(data,id)
  {
  	var fd=new FormData();
    fd.append('billno',this.billno);
    fd.append('iname',data.iname);
    fd.append('batchno',data.bno);
    fd.append('strquantity',data.quantity);
     

     this.lara.inssuborder(fd).subscribe((datas:any[])=>{
     console.log(datas[0].repply);
     
       id.reset();
});
  }

}
