import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LaravelService } from '../laravel.service';

@Component({
  selector: 'app-dis-medi-det',
  templateUrl: './dis-medi-det.component.html',
  styleUrls: ['./dis-medi-det.component.css']
})
export class DisMediDetComponent implements OnInit {
id:string="";
details=[];
  constructor(private route:ActivatedRoute,private lara:LaravelService) { }

  ngOnInit() 
  {
  	this.id= this.route.snapshot.paramMap.get('id');

  	var fd=new FormData();
  	fd.append("id",this.id);
  	this.lara.getdet(fd).subscribe((datas:any[])=>{
  		this.details=datas;
  		console.log(datas);
  	});
  }

}
