import { Component, OnInit } from '@angular/core';
import { LaravelService } from '../laravel.service';

@Component({
  selector: 'app-medicinedetails',
  templateUrl: './medicinedetails.component.html',
  styleUrls: ['./medicinedetails.component.css']
})
export class MedicinedetailsComponent implements OnInit {
details=[];
  constructor(private lara:LaravelService) { }

  ngOnInit() {
  	this.lara.getmedidetails().subscribe((data:any[])=>{
    //console.log(data);
    this.details=data;
    console.log(this.details);
  });
  }

  medicineDetails(data,id)
  {
  	var fd=new FormData();
     fd.append('mediid',data.mid);
     fd.append('batch',data.batch);
     fd.append('mfgdt',data.mfg_dt);
     fd.append('expdt',data.Exp_dt);
     fd.append('pts',data.pts);
     fd.append('ptr',data.ptr);
     fd.append('mrp',data.mrp);
     fd.append('quantity',data.quantity);

     this.lara.inmedidetails(fd).subscribe((datas:any[])=>{
    console.log(datas);
  });
  }
}
